.mask {
    width: 100%;
    height: 100vh;
    position: relative;
    
}

.into-img {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
    
}

.mask::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.3;
}

.hero {
    height: 100%;
    width: 100%;
}

.hero .content {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
    
}

.hero .content h1 {
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;

}

.hero .content h2 {
    font-size: 2rem;
    font-weight: 300;
    color: aliceblue;
}

.hero .content p {
    font-size: 1.5rem;
    font-weight: 300;
    margin-bottom: 50px;
}

.content .btn {
    margin: 5rem 0.2rem;

}

@media screen and (max-width:640px) {
    .hero .content h1 {
        font-size: 3rem;

    }

    .hero .content p {
        font-size: 1.4rem;
      
    }

}